@import 'style-mixins';
html,
body {
    height: 100%;
    width: 100%;
    margin: 0;
}

.mainheader {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 10000;
    .header {
        position: relative;
        z-index: 3000;
        padding-top: $top-page-padding;
        .logo {
            @include propertyDyn(padding-left, 40, 'px'); //padding-left: 40px;
            img {
                width: 100%;
                max-width: 294px;
            }
        }
        .search {
            //background: green;
            //BARRA BUSQUEDA
            .css-input {
                border-style: solid;
                border-radius: 50px;
                @include fontsize(1.2em, 1.5em, 2em);
                background: $blue-trans-background;
                border-color: #8c8c8c;
                border-width: 1px;
                color: #dadada;
                padding: 10px;
                width: 100%;
                font-family: Calibri;
            }
            .css-input:focus {
                outline: none;
            }
            .clear{
                position: absolute;
                height: 30px;
                width: 30px;
                //background: red;
                right: 5%;
                top: 50%;
                transform: translateY(-15px);
                color: white;
                font-size: 2em;
                i{
                    position: absolute;
                    right: 2px;
                    top: -2px;
                }
                cursor:pointer;
            }
            .clear.filterLoading{
                background: url('/images/loader-1.gif');
                background-size: cover;
            }
        }
        .view-user {
            .phase-text {
                @include fontsizeaut(1em);
            }
            .user {
                .avatar * {
                    @include propertyDyn(height, 70, 'px'); //height: 70px;
                    border-radius: 50px;
                    @include propertyDyn(margin-top, -10, 'px'); //margin-top: -10px;                    
                }
                .welcome,
                .username {
                    text-align: right;
                    font-family: Calibri;
                    color: white;
                }
                .welcome {
                    @include propertyDyn(margin-top, -15, 'px'); //margin-top: -15px;   
                    @include fontsizeaut(2.3em);
                }
                .username {
                    @include propertyDyn(margin-top, -11, 'px'); //margin-top: -11px;
                    @include fontsizeaut(1.3em);
                }
            }
        }
    }
}

#map {
    height: 100%;
    width: 100%;
    >.filters {
        margin-top: 100px;
    }
    @include transAnim($animation-time); //opacity: 0.5;
    .leaflet-marker-pane {
        opacity: initial;
    }
    div.leaflet-pane.leaflet-tile-pane {
        -webkit-filter: brightness(3) contrast(100%) !important;
        filter: brightness(3) contrast(100%) !important;
        img {
            opacity: 1 !important;
        }
    }
    div.leaflet-pane.leaflet-marker-pane,
    div.leaflet-pane.leaflet-popup-pane {
        opacity: 1;
    }
    #background {
        background: #002b50;
        background-image: url(/images/back-pattern.png);
        background-repeat: repeat;
        height: 10000px;
        width: 10000px;
        top: -4000px;
        left: -4000px;
        z-index: 1000;
        opacity: 0.3;
        position: absolute;
    }
    .marker-cluster-small {
        background: initial;
        border: solid 2px $green-corp;
        div {
            background: $green-corp;
            color: white;
            font-weight: bold;
            margin-left: 3px;
            margin-top: 3px;
        }
    }
}

.home-container {
    //z-index: 2000;
    //position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    
    .mobile{
        @include onlyMobile();
        height: 200px;
        #mobileCarousel{
            width: 100%;
        }
        .project-data>div, .actual-phase>div, .organization>div{
            margin: 5px !important;
        }
    }
    .desktop{@include onlyDesktop();}

    .footer {
        position: absolute;
        z-index: 3000;
        bottom: 0;
        margin: 0;
        width: 100%;
        .row {
            padding-top: 10px;
            margin-left: 10px;
            margin-right: 10px;
        }
        >.row:hover {
            cursor: pointer;
        }
        .green-link {
            color: $green-corp;
            text-transform: uppercase;
            text-align: right;
            font-family: Calibri-Bold; //@include fontsize(1.3em,1.5em,1.7em);   
            @include fontsizeaut(1.8em);
            a {
                color: $green-corp;
            }
        }
        .box-icon:before {
            //@include fontsize(1.4em,1.6em,1.8em);      
            @include fontsizeaut(1.8em);
        }
        .box-title {
            //font-size: 1.1em;
            //@include fontsizeaut(1.8em);
            //@include fontsize(1.4em,1.6em,1.8em);
            @include fontsizeaut(2em);
            text-transform: uppercase;
            margin-left: 10px;
            font-family: Calibri-Bold;
        }
        >div {
            padding-right: 0;
            padding-left: 0;
            color: white;
        }
        .project-data,
        .actual-phase,
        .organization,
        .all {
            >div {
                height: 260px;
                margin: 30px;
                background: $blue-trans-background;
            }
        }
        .medium-title {
            font-family: Calibri-Bold;
            display: block;
            text-transform: uppercase;
            text-align: center;
            font-weight: bold; //font-size: 2.3em;
            @include fontsizeaut(2.1em);
        }
        .big-number {
            display: block;
            text-align: center;
            font-weight: lighter; //font-size: 5.8em;
            @include fontsizeaut(5.8em);
            font-family: Calibri;
            .million-currency,
            .percent-symbol {
                //font-size: 0.3em;
                @include fontsizeaut(.3em);
            }
        }
        .small-number {
            display: block;
            text-align: center;
            font-weight: 50; //font-size: 2em;
            @include fontsizeaut(2em);
            font-family: Calibri;
        }
        .sub-title,
        .sub-data {
            display: block;
            text-transform: uppercase;
            font-family: Calibri; //font-size: 1.4em;
            @include fontsizeaut(1.4em);
        }
        .sub-data {
            color: $green-corp;
            font-weight: lighter;
        }
        .actual-phase {
            .chart-number{
                position: absolute;
                top: 9%;
                left: 0;
                right: 0;
                bottom: 0;
                text-align: center;
                font-size: 4em;
            }
            .phase-progress {
                margin: 0 auto;
                //@include propertyDyn(width, 10, 'em');//width: 50px;
                @include propertyDyn(height, 9, 'em');//height: 50px;
                //background-color: red;
                .highcharts-container{
                    margin: 0 auto;
                }
            }
            .tendering {
                display: block;
                text-transform: uppercase; //font-size: 1.2em;
                @include fontsizeaut(2em);
                color: $green-corp;
                text-align: center;
                font-family: Calibri;
            }
        }
        .slide-avatar-icon {
            margin: 0 auto;
            border-radius: 50px;
            //height: 100px;
            @include square(100, 'px');
            background-position: center center;
            background-repeat: no-repeat;
        }
        .slide-avatar-name,
        .slide-avatar-charge {
            display: block;
            text-align: center;
            font-family: Calibri;
        }
        .slide-avatar-name {
            color: $green-corp; //font-size: 1.8em;
            @include fontsizeaut(1.8em);
            position: relative;
            top: 10px;
        }
        .slide-avatar-charge {
            //font-size: 2em;
            @include fontsizeaut(2em);
        }
        .carousel-control-prev,
        .carousel-control-next {
            position: absolute;
            top: 120px;
        }
        .carousel {
            .fa-angle-left,
            .fa-angle-right {
                color: $green-corp; //font-size: 3em;
                @include fontsizeaut(3em);
            }
        }
    }
}

//Styles to filter projects in map
.filterPhaseDisable{
    display:none;
}

.filterProjectTypeDisable{
    display:none;
}

.filterStatusProjectDisable{
    display:none;
}

//Left Menu Filters
.filters {
    position: relative;
    z-index: 4000;
    left: 40px;
    
    .phase-filter,
    .project-type-filter,
    .project-status-filter {
        @include propertyDyn(padding-top, 30, 'px'); //padding-top:30px;
    }

    .phase-views {
        @include propertyDyn(padding-top, 20, 'px'); //padding-top: 20px;
    }
    .filter {
        @include propertyDyn(padding-left, 40, 'px'); //padding-left: 40px;
        @include propertyDyn(padding-bottom, 3, 'px'); //padding-bottom: 3px;
        .phase-color {
            display: inline-block;
            @include square(20, 'px');
            border-radius: 20px;
            background: white;
            margin: 5px 5px 0 0;
        }
        .phase-text {
            font-family: Calibri-Bold;
            text-transform: uppercase;
            color: white;
            margin-right: 10px; //font-size: 1.8em;
            @include fontsizeaut(1.8em);
        }
        .filterBy {
            background: white;
            border-radius: 50px;
            color: black; //font-size: 0.75em;
            @include fontsizeaut(0.75em);
            font-family: Calibri-Bold;
            text-transform: uppercase;
            padding: 2px 4px;
            .filterBy-icon {
                margin-left: 10px;
                color: gray;
            }
        }
        .round-project-type{
            margin: 5px 5px 0px 0px; 
            padding: 2px; 
            border-radius: 50%;
            background: white;

            @include propertyDyn(height, 20, 'px');
            @include propertyDyn(width, 20, 'px');
        }
    }
    .toggle-container.active,
    .toggle-container.active>div {
        @include transAnim($animation-time);
        border-color: $enabled-color;
    }
    .toggle-container.active>div {
        @include transAnim($animation-time);
        background: $enabled-color;
        @include propertyDyn(left, 27, 'px'); //left: 27px;
    }
    .filter-by {
        margin-right: 10px;
        display: inline-block;
        width: 40px;
        height: 20px;
        background: white;
    }
    .toggle-container {
        @include propertyDyn(margin-right, 25, 'px'); //margin-right: 10px;
        display: inline-block;
        @include propertyDyn(width, 50, 'px'); //width: 50px;
        @include propertyDyn(height, 25, 'px'); //height: 25px;
        border-radius: 50px;
        position: relative;
        top: 3px;
        border: solid $disabled-color 1px;
        cursor:pointer;
        >div {
            @include transAnim($animation-time);
            @include propertyDyn(top, 2, 'px'); //top: 2px;
            @include propertyDyn(left, 4, 'px'); //left: 4px;
            border-radius: 10px;
            @include square(18, 'px');
            border: solid $disabled-color 1px;
            background: $disabled-color;
            position: relative;
        }
    }
}

//RELOJ
#timestamp {
    position: absolute;
    z-index: 4000;
    bottom: 3%;
    right: 3%;
    #HORA>p,
    #DOHAGMT>p {
        font-family: Calibri-Bold;
        color: white;
        text-transform: uppercase;
    }
    #HORA {
        @include propertyDyn(height, 100, 'px'); //height: 100px;
        //font-size: 80px;
        @include fontsizeaut(6em);
        margin-bottom: 0;
    }
    #DOHAGMT {
        //font-size: 16px;
        @include fontsizeaut(1.6em);
        margin-top: 0;
    }
}

//OCULTA ZOOM
.leaflet-control-container {
    display: none;
}


