        -webkit-mask-image: url(/images/icons/marker.png);
        -webkit-mask-size: cover;
        position: relative;
        @include propertyDyn(top, -47, 'px'); //top: -47px;
        @include propertyDyn(left, -19, 'px'); //left: -19px;
    }
    .circle-white {
        border-radius: 50px;
        background: white;
        @include square(30, 'px');
        position: relative;
