} //POPUP PROYECTO
.leaflet-popup {
    .leaflet-popup-close-button {
        display: none
    }
    border: solid 1 px white;
    border-radius: 0;
    background: $blue-trans-background !important;
    .leaflet-popup-content-wrapper {
        margin: 10px;
        background: none !important;
        box-shadow: none !important;
        .leaflet-popup-content {
            width: 100%;
            background: none !important;
            color: white;
            font-family: Calibri;
            >.row {
                @include propertyDyn(margin-top, 7, 'px'); //margin-top: 7px;
            }
            >img {
                @include propertyDyn(width, 301, 'px'); //width: 301px;
            }
            .icon-type {
                @include square(40, 'px');
                margin-top: 2px;
                .icon-container {
                    border-radius: 50px; //border: solid 4px red;//Asignar por js (estado projecto)
                    background: white;
                    @include square(40, 'px');
                }
                .icon {
                    @include square(25, 'px');
                    //background: red; //Asignar por js (estado projecto)
                    position: relative; //top: -33px;
                    @include propertyDyn(top, -33, 'px');
                    @include propertyDyn(left, 7, 'px'); //left: 7px;
                    //-webkit-mask-image: url(/images/icons/construction.png); //Asignar por js (tipo proyecto)
                    -webkit-mask-size: cover;
                }
            }
            .title,
            .descrip,
            .quantity,
            .currency {
                font-family: Calibri;
                text-transform: uppercase;
            }
            .title {
                //font-size: 2.1em;
                @include fontsizeaut(2.1em);
            }
            .descrip {
                @include propertyDyn(margin-top, -10, 'px'); //margin-top: -10px;
                @include fontsizeaut(1em); //font-size: 1em;
            }
            .quantity-currency {
                //@include propertyDyn(margin-top, 4, 'px'); //margin-top: 4px;
                text-align: right;
                .amount {
                    /*font-size: 2.2em;*/
                    @include fontsizeaut(2.2em);
                }
                .million-currency {
                    /*font-size: 1.8em;*/
                    @include fontsizeaut(1.8em);
                }
            }
            .project-show-all{
                position: absolute;
                right: 11px;
                bottom: 5px;
                color: $green-corp;
                font-size: 1.2em;
                cursor:pointer;
            }
        }
    }
}

.leaflet-popup-tip {
    background: $blue-trans-background !important;
}


/* MIDDLE BUTTONS */

.middle-button {
    color: $green-corp;
    position: fixed;
    top: 40%;
    @include square(91, 'px');
    cursor:pointer;
    z-index: 999;
     :hover {
        cursor: pointer;
    }
}

.middle-button.left {
    left: 0;
    background: url('/images/icons/back_to_map.png');
    background-size: cover;
}

.middle-button.right {
    right: 0;
    background: url('/images/icons/phases_go.png');
    background-size: cover;
}
.middle-button.backBackoffice{
    left: 0;
    background: url('/images/icons/back_to_backoffice.png');
    background-size: cover;
}

.projects-modal {
    .phase-name{
        padding-left: 0;
        @include propertyDyn(margin-top, 1.5, 'em');
        @include propertyDyn(margin-bottom, 1.5, 'em');
        >span{
            display: inline-block;
            //@include propertyDyn(height, 60, 'px');
            padding: 1em;
            width: 100%;
            background: rgba(0, 23, 43, 0.93);//$blue-trans-background;
            color: white;
            font-family: Calibri;
            text-transform: uppercase;
        }
    }
    .phase-name:last-of-type{
        padding-right: 0;
    }         
    .milestone-unit{
        border-radius: 4px;
        padding-left: 0;
        border-left: 4px solid $green-corp;
        @include propertyDyn(margin-bottom, .5, 'em');
        >span{
            display: inline-block;
            //@include propertyDyn(height, 60, 'px');
            padding: 1em;
            width: 100%;
            background: rgba(70, 138, 255, 0.295);
            color: white;
            font-family: Calibri;
            text-transform: uppercase;
        }
        cursor:pointer;
    }
    .phase-progress-bar {
        margin-top: 150px;
        > div{
            padding-left: 0;
            padding-right: 0;            
        }
   
        .progress {
            background-color: $blue-trans-background !important;
            .progress-bar-success {
                background-color: $green-corp;
            }
            .progress-bar-warning {
                background-color: $yellow-corp;
            }
            .progress-bar-danger {
                background-color: $red-corp;
            }
            .today {
                position: absolute;
                top: -20px;
                color: white;
                height: 37px;
                border-left: solid 2px rgba(255, 255, 255, 0.34);
                padding-left: 4px;
            }
            .phase-check {
                position: absolute;
                transform: rotate(-45deg) translateY(-35px) translateX(-35px);
                left: 3%;
                margin-top: -120px;
                .phase-title {
                    color: white;
                    padding-left: 25px;
                    width: 110px;
                    text-transform: uppercase; //font-size: .9em;             
                    @include fontsizeaut(.9em);
                    display: table-cell;
                    height: 60px;
                    vertical-align: bottom;                    
                }
                .phase-status {
                    @include square(23, 'px');
                    border: solid 1px white;
                    background-color: white;
                }

            }
            .phase-check:last-of-type{
                width: 110px !important;
            }

            
        }
    }
}


