    .th_project_list_image {
        @include propertyDyn(width, 135, 'px');// width: 135px;        
    }

    .project_list_image {
        
        @include propertyDyn(width, 135, 'px');// width: 135px;
        @include propertyDyn(height, 107, 'px');// height: 107px;
        
    }

    /*
        .dataTables_scrollHead{
            margin-bottom: 20px;
        }
        */
    table.dataTable.row-border tbody th,
