/* FORMS ****************************************************************/
#forms{
    position: absolute;
    top:0;
    left:0;
    right:0;
    background: white;


    .vertical-alignment-helper {
        display:table;
        height: 100%;
        width: 100%;
        pointer-events:none; /* This makes sure that we can still click outside of the modal to close it */
    }
    .vertical-align-center {
        /* To center vertically */
        display: table-cell;
        vertical-align: middle;
        pointer-events:none;
    }
    .modal-content {
        /* Bootstrap sets the size of the modal in the modal-dialog class, we need to inherit it */
        width:inherit;
        max-width:inherit; /* For Bootstrap 4 - to avoid the modal window stretching full width */
        height:inherit;
        /* To center horizontally */
        margin: 0 auto;
        pointer-events: all;
    }


    .task-area.tasks:nth-child(2n+1){
        background: #f5f5f5;
    }
    .task-area.tasks:nth-child(2n){
        background: white;
    }


    .blueicon{
        background: $blue-icons;
        color: white;
        @include square(45, 'px');
        border-radius: 50px;
        text-align: center;
        display: inline-block;
        @include propertyDyn(margin-right, 7, 'px');//margin-right: 7px;
        font-weight: 100;
        vertical-align: middle;
        >i{
            position: relative;
            //top:2px;
        }
        >.upload-icon, >.download-icon{
            background:white;
            @include square(30, 'px');
            -webkit-mask-size: cover;
            position: relative;
            margin: 0 auto;
        }
        >.upload-icon{
            top: 4px;
            -webkit-mask-image: url(/images/icons/upload_ic.png);
            cursor: pointer;
        }
        >.download-icon{
            cursor: pointer;
            top: 10px;
            -webkit-mask-image: url(/images/icons/download_ic.png);
        }
        
    }
    .blueicon.disabled{
        background: lightgray;
        color:black;
        >.upload-icon{
            background: black;
            cursor: pointer;
        }
        >.download-icon{
            background: black;
            cursor: default;
        }
    }   
    .circle-ok, .circle-review, .circle-cancel, .circle-na, .circle-done{
        @include transAnim($animation-time); 
    }
    .switch-container-1{  
        @include square(38, 'px');  
        border:solid 1px black;
        border-radius: 50px;
        @include propertyDyn(padding, 3, 'px');//padding: 3px;

        float: right;
        margin-right: 32px;
    }    


    .circle-na, .circle-done{
        border-radius: 50px;
        @include square(30, 'px');
        text-transform: uppercase;
        @include fontsizeaut(.6em);
        text-align: center;
        background: black;
        color:white;
        cursor: no-drop;
        span{
            position: relative;
            @include propertyDyn(top, 7, 'px');
        }
    }

    .circle-na.approval{
        
        @include propertyDyn(margin-top, 22, 'px');//margin-top:22px;

        span{
            position: relative;
            @include propertyDyn(top, 1, 'px');
            @include propertyDyn(font-size, 0.6, 'em');//margin-top:22px;
            @include propertyDyn(margin-top, 1, 'px');
        }
        
    }
    
    .circle-ok, .circle-review, .circle-cancel{
        border-radius: 50px;
        @include square(30, 'px');
        text-transform: uppercase;
        @include fontsizeaut(.6em);
        text-align: center;
        background: black;
        color:white;
        cursor: no-drop;
        span{
            position: relative;
            @include propertyDyn(top, 1, 'px');
        }
    }

    .circle-na, .circle-done{
        background: black;
        color:white;
    }
    .circle-done{
        @include propertyDyn(margin-top, 22, 'px');
    }
    .circle-na.active{
        background: #c7b38e;
        color:black;        
    }
    .circle-done.active{
        background: $green-corp;
    }
  
  .circle-review, .circle-cancel{
        @include propertyDyn(margin-top, 22, 'px');//margin-top:22px;
    }
    .circle-ok.active{
        background: $green-corp;
    }
    .circle-review.active{
        background: $yellow-corp;
        color: black;
    }
    .circle-cancel.active{
        background: $red-corp;
    }

    .switch-container-2{
        @include propertyDyn(height, 90, 'px');//height: 90px;
        @include propertyDyn(width, 38, 'px');//width: 38px;        
        border:solid 1px black;
        border-radius: 50px;
        @include propertyDyn(padding, 3, 'px');//padding: 3px;

        float: right;
        margin-right: 32px;
    } 

    .switch-container-3{
        @include propertyDyn(height, 142, 'px');//height: 90px;
        @include propertyDyn(width, 38, 'px');//width: 38px;        
        border:solid 1px black;
        border-radius: 50px;
        @include propertyDyn(padding, 3, 'px');//padding: 3px;

        float: right;
        margin-right: 32px;
    }         


    .body{
        width: 100%;
        .headgreen{
            width: 100%;
            background: rgba(157, 255, 0, 0.39);
            @include propertyDyn(height, 7, 'em');//height: 7em;
            position: absolute;
        }
        .headwallpaper{
            width: 100%;
            @include propertyDyn(height, 20, 'em');//height: 20em;
            background: url(https://ultimahora.es/sfAttachPlugin/getCachedContent/id/139907);
            background-size: 100% auto;
            @include propertyDyn(padding-top, 7, 'em');//padding-top: 7em;
            .country, .proyect, .qty-currency{
                text-transform: uppercase; 
                @include fontsizeaut(4em);
                color:white;
            }
            .country{font-family: Calibri-Bold;}
            .proyect{
                font-family: Calibri;
                font-weight:100;}
            .qty-currency{
                text-align: right;
                font-family: Calibri-Bold;
            }

        }
        .form-breadcrumb{
            @include propertyDyn(margin-top, 3, 'em');//margin-top: 3em;
            text-transform: uppercase; 
            font-family: Calibri-Bold;
            font-weight:bolder;
            @include fontsizeaut(2.2em);
            a{
                color: $green-corp;
            }
            i{
                color:black;
            }
        }

        .section-title{
            @include propertyDyn(margin-top, 1, 'em');//margin-top: 1em;
            @include propertyDyn(margin-bottom, .8, 'em');//margin-top: 1em;
            color: $green-corp;
            @include fontsizeaut(3em);
        }

        .prev-button, .next-button{
            display: inline-block;
            background: $blue-icons;
            border-radius: 20px;
            color: white;
            padding: 10px 12px;
            cursor: pointer;
            @include fontsizeaut(1em);//font-size: .6em;
            text-transform: uppercase;
        }
        .next-button{
            position: absolute;
            right: 0;
        }
        .footer{
            margin: 2em 0;
        }

        
        // TASK *******************************************************************
        .task-area{
            @include propertyDyn(margin-top, 3, 'em');//margin-top: 1em;
            @include propertyDyn(margin-bottom, 3, 'em');//margin-top: 1em;
            .task-header{
                font-family: Calibri;
                .task-order{
                    margin: auto;
                    text-align: right;
                    >span{
                        text-align: center;
                        background: lightgray;
                        border-radius: 50px;
                        display:inline-block;
                        @include square(45, 'px');
                        vertical-align: middle;
                        @include fontsizeaut(2.1em);
                    }

                }

                .subtask-order{
                    margin: auto;
                    text-align: right;
                    >span{
                        text-align: center;
                        background: lightgray;
                        border-radius: 25px;
                        display:inline-block;
                        @include square(25, 'px');
                        vertical-align: middle;
                        @include fontsizeaut(1.5em);
                    }

                }


                .task-description{
                    @include fontsizeaut(3.1em);
                    font-weight: lighter;
                    > span{
                        display:block;
                    }
                    .last-update{
                        margin-top:-10px;
                        @include fontsizeaut(0.6em); 
                        color:#7aa7c5;
                        font-style: italic;
                    }
                }

            }
            .task-data{
                @include fontsizeaut(1.8em);
                @include propertyDyn(margin-top, .3, 'em');
                .title-single-data{
                    font-family: Calibri-Bold;
                    font-weight: bolder;
                    @include fontsizeaut(1.2em);
                }
                .input-group{
                    //@include propertyDyn(height, 1.8, 'em');//height: 1.8em;
                }
                .comment-button{
                    >span{
                        display: inline-block;
                        background: $blue-icons;
                        border-radius: 20px;
                        color: white;
                        padding: 10px 12px;
                        @include fontsizeaut(.6em);//font-size: .6em;
                        text-transform: uppercase;
                    }
                }
                .comment-area >.form-control{
                    border: 1px solid light gray !important;
                    border-radius: 20px !important;
                }
                span.disabled{
                    color:lightgray;
                }
                .input-responsible{
                    position: relative;
                    @include propertyDyn(top, 9, 'px');//top: 25px;
                    /*
                    input{
                        @include fontsizeaut(1.2em);
                    }
                    */
                }
            }
        }
        
        .endline{
            border-bottom: solid 1px $green-corp;
            height: 2px;
            padding-top: 4em;
        }       
    }


    /* Icon when the collapsible content is shown */
    .section-header > .task-description > span:after{
        font-family: "FontAwesome";
        content: "\f139";
        float: right;
        margin-right: 30px;
    }
    /* Icon when the collapsible content is hidden */
    .section-header.collapsed > .task-description > span:after{
        content: "\f13a";
    }


    /* Icon when the collapsible content is shown */
    .div-section-title > span:after{
        font-family: "FontAwesome";
        content: "\f139";
        float: right;
        margin-right: 30px;
    }

    /* Icon when the collapsible content is hidden */
    .div-section-title.collapsed > span:after{
        content: "\f13a";
    }

    /* Make Select2 boxes match Bootstrap3 heights: */
    .select2-selection__rendered {
        line-height: 32px !important;
    }

    .select2-selection {
        height: 38px !important;
    }

    .select2-container--default .select2-selection--single .select2-selection__arrow {
        height: 34px;
        position: absolute;
        top: 1px;
        right: 1px;
        width: 20px;
    }

    .select2-container--default .select2-selection--single .select2-selection__clear {
        cursor: pointer;
        float: right;
        font-weight: bold;
        line-height: 28px;
    }

    .modal-header{
        background: $green-corp;
        color: white;
    }

    .btn-primary{
        background: $blue-icons;
        border-color: $blue-icons;
    }

    .tooltip-task-info{
        font-size: 0.65em;
        padding-left:20px;
    }

}
